<template>
<transition name="app">
<div class="ui-page">
  <header-bar>
    <icon-button slot="left" @click.native="showNav" icon="menu"></icon-button>
    <span>long-ui</span>
  </header-bar>
  <scroll-view ref="scroller">
    <content-block>
      long-ui 是基于 vue 开发的material design ui 库。
    </content-block>
    <template v-for="group in menus">
      <content-title>
        {{group.title}}
      </content-title>
      <list>
        <item-cell link v-for="(menu, index) in group.children" :key="index" @click.native="goTo(menu.link)">
          <item-media>
            <i class="logo">L</i>
          </item-media>
          <item-title>
            {{menu.title}}
          </item-title>
        </item-cell>
      </list>
    </template>
  </scroll-view>
</div>
</transition>
</template>

<script>
import Vue from 'vue'
let scrollTop = 0
export default {
  data () {
    return {
      navShow: false,
      menus: [{
        title: 'Basic',
        children: [{
          title: 'Icon',
          link: '/icon'
        }, {
          title: 'Badge',
          link: '/badge'
        }, {
          title: 'Button',
          link: '/button'
        }, {
          title: 'Float Button',
          link: '/floatButton'
        }, {
          title: 'Circular',
          link: '/circular'
        }, {
          title: 'Refresh Control',
          link: '/refreshControl'
        }, {
          title: 'Infinite Scroll',
          link: '/infiniteScroll'
        }]
      }, {
        title: 'CSS',
        children: [{
          title: 'Header Bar',
          link: '/headerBar'
        }, {
          title: 'Tab Bar',
          link: '/tabBar'
        }, {
          title: 'Search Bar',
          link: '/searchBar'
        }, {
          title: 'Bottom Nav',
          link: '/bottomNav'
        }, {
          title: 'SnackBar',
          link: '/snackBar'
        }, {
          title: 'List View',
          link: '/listView'
        }, {
          title: 'Card',
          link: '/card'
        }, {
          title: 'Grid',
          link: '/grid'
        }, {
          title: 'Content Block',
          link: '/contentBlock'
        }]
      }, {
        title: 'Forms',
        children: [{
          title: 'Text Field',
          link: '/inputs'
        }, {
          title: 'Switch',
          link: '/switch'
        }, {
          title: 'Range',
          link: '/range'
        }, {
          title: 'Checkbox And Radio',
          link: '/check'
        }, {
          title: 'Select Field',
          link: '/select'
        }, {
          title: 'Date Select',
          link: '/dateSelect'
        }]
      }, {
        title: 'Popups',
        children: [{
          title: 'Overlay',
          link: '/overlay'
        }, {
          title: 'Toast',
          link: '/toast'
        }, {
          title: 'Modal',
          link: '/modal'
        }, {
          title: 'Popup',
          link: '/popup'
        }, {
          title: 'Action Sheet',
          link: '/actionSheet'
        }, {
          title: 'Popover',
          link: '/popover'
        }, {
          title: 'Indicator',
          link: '/indicator'
        }, {
          title: 'Picker',
          link: '/picker'
        }]
      }]
    }
  },
  activated () {
    Vue.nextTick(() => {
      this.$refs.scroller.$el.scrollTop = scrollTop
    })
  },
  deactivated () {
    scrollTop = this.$refs.scroller.$el.scrollTop
  },
  methods: {
    showNav () {
      this.$parent.navShow = true
      this.$emit('menuShow')
    },
    goTo (link) {
      this.$router.push(link)
    }
  }
}
</script>

<style lang="css">
.logo{
  display: flex;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  background-color: #474a4f;
  color: #FFF;
  font-size: 18px;
  line-height: 1;
  font-weight: bold;
  border-radius: 50%;
}
.ui-flex {
  display: inline-block;
  width: 100%;
  height: auto;
}
.nav-icon-logo {
  width: 80px;
  height: 80px;
  background-color: #bbbfc4;
  color:#FFF;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 50px;
}

.nav-demo-title{
  font-size: 16px;
  margin-top: 16px;
}

.nav-demo-sub-title{
  color: #d3d6db;
  font-size: 14px;
}
</style>



